import React, { useState, useEffect } from "react";
import { Select, Spin, Row, Tree, Menu, Dropdown, Tabs, Button,Alert } from 'antd';
import styles from './AdminPortal.module.css';
import UserService from "../services/user.service";
import download from "downloadjs";
//import { Button } from "bootstrap";

const ExportBoard = () => {
  const [content, setContent] = useState("");
  const [loading, setLoading] = useState(false);
  const [checkedNodes,setCheckedNodes] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");
  const [success, setSuccess] = useState(false)

  useEffect(() => {

    setLoading(true);
    UserService.getDbs("both").then(
      (response) => {
        setContent(response.data);
        setErrorMessage("");
        setLoading(false)
        
      },
      (error) => {
        const errorMsg =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        setErrorMessage(errorMsg);
        setLoading(false);
      }
    );
  }, []);

  const onCheck = (key,e) => {

    setCheckedNodes(e.checkedNodes.filter(node => !node.children)
                                 .map(node =>{ return {type: node.type, subType: node.subType}}))


  }

  const onClickExport = () => {
    setErrorMessage("");
    setSuccess(false)

    UserService.backupDbs(checkedNodes).then(
      (response) => {
        //setContent(response.data);
        setErrorMessage("");
        //setLoading(false)
        setSuccess(true)
        
      },
      (error) => {
        const errorMsg =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        setErrorMessage(errorMsg);
        //setLoading(false);
      }
    );



  }

  const onClickTakeBackup = () => {

    setErrorMessage("");
    setSuccess(false)

    UserService.getBackup().then(

      (response) => {
        //setContent(response.data);
        setErrorMessage("");
        //setLoading(false)
        setSuccess(true)
        download(response.data, "backup.zip"); 
      },      
      (error) => {
        const errorMsg =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        setErrorMessage(errorMsg);
        //setLoading(false);
      }


    )


  } 


  return (
    <div className="container">
      <Row>
      {errorMessage&&<Alert message={errorMessage} style={{ marginBottom: "10px" }} type="error" />}
      </Row>
      <Row>
      {loading ? <Spin tip="Ładowanie..." /> :

        <Tree treeData={content}

        onCheck={onCheck}

        checkable


        />
      }
      </Row>
      <Row>
        <Button disabled={!checkedNodes.length>0} onClick={onClickExport}>Ekspoprt</Button>
        {success?<Button onClick={onClickTakeBackup}>Pobierz</Button>:null}
        </Row>

    </div>
  );
};

export default ExportBoard;