//import axios from "axios";
import axios from "../service/http-common";
import download from "downloadjs";
import authHeader from "./auth-header";

const API_URL = "/api/test/";

const getPublicContent = () => {
  return axios.get(API_URL + "all");
};

const getUserBoard = () => {
  return axios.get(API_URL + "user", { headers: authHeader() });
};

const getModeratorBoard = () => {
  return axios.get(API_URL + "mod", { headers: authHeader() });
};

const getAdminBoard = () => {
  return axios.get(API_URL + "admin", { headers: authHeader() });
};

const postDbs = (data) => {
return axios.post(API_URL + "dbs",  data, { headers: authHeader() })
};

const patchDbs = (data) => {
  return axios.patch(API_URL + "dbs",  data, { headers: authHeader() })
  };

  const deleteDbs = (data) => 
{

  return axios({
    method: 'DELETE',
    url: API_URL+"dbs",
    data: data,
    headers: authHeader()
})



}
     
const backupDbs = (data) => {
  return axios.post(API_URL + "backupdbs",  data, { headers: authHeader() })
  };
        




const getDbs = (ipVer) => {
  //return axios.get(API_URL + "dbs", { headers: authHeader() }, { params: { "type": ipVer }});
  return axios.get(API_URL + "dbs",{ params: {type: ipVer},  headers: authHeader() }   );
  //{ params: { answer: 42 } }
};


const postUser = (user) => 
{
  return axios.post(API_URL+ "user", user,{ headers: authHeader() })
}



const patchUser = (user) => 
{
  return axios.patch(API_URL+ "user", user,{ headers: authHeader() })
}

const deleteUser = (user) => 
{

  return axios({
    method: 'DELETE',
    url: API_URL+"user",
    data: user,
    headers: authHeader()
})

}

const getBackup = () => {

  return axios({
    method: 'get',
    url: API_URL + `backupdbs`,
    responseType: 'blob', 
    headers: authHeader()
  })


}

const getRaport = (fileName) => {
  //return axios.get(API_URL + "dbs", { headers: authHeader() }, { params: { "type": ipVer }});
  //return axios.get(API_URL + `raport/${fileName}.txt`,{responseType: 'blob', headers: authHeader() }    )
  return axios({
    method: 'get',
    url: API_URL + `raport/${fileName}.txt`,
    responseType: 'blob', 
    headers: authHeader()
  })
  .then(response => {  
    // Saving file to working directory  
    console.log(response);
    download(response.data, "raport.txt"); 
    }) 
    .catch(error => {  
      console.log("nie udało się zapisac pliku ");  
  });  

  //{ params: { answer: 42 } }
};

export default {
  getPublicContent,
  getUserBoard,
  getModeratorBoard,
  getAdminBoard,
  getDbs,
  getRaport,
  postUser,
  deleteUser,
  patchUser,
  postDbs,
  patchDbs,
  deleteDbs,
  backupDbs,
  getBackup
};
