import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Input, Checkbox, Alert } from 'antd';
//import axios from 'axios';
import axios from "../../service/http-common";
import { PoweroffOutlined } from '@ant-design/icons';
import authHeader from "../../services/auth-header";
import UserService from "../../services/user.service";

const CheckboxGroup = Checkbox.Group;



const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 22,
    },
};

export default function ModaUserEdit(props) {

    const [form] = Form.useForm();


    const [isLoading, setIsLoading] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const [message, setMessage] = useState("");
    const [changePass,setChangePass] = useState(false);


    useEffect(() => {
        form.setFieldsValue({
            username: props.node.username,
            email: props.node.email


        })
    });


    const handleOk = (values) => {

        form.submit();

    }



    const formOk = () => {

        setIsLoading(true);

        const error = form.getFieldsError();

        console.log("///////////")
        console.log(error)
        console.log("///////////")

        let data = form.getFieldsValue();
        //data = { ...data }


        console.log("/////////////");
        console.log(data);
        console.log("/////////////");

        UserService.patchUser(data)
            .then(
                (response) => {
                    setMessage(response.data.message);
                    setIsLoading(false);
                    setErrorMessage("");
                    props.handleCancel()
                    props.getUsers()
                },
                (error) => {
                    const resMessage =
                        (error.response &&
                            error.response.data &&
                            error.response.data.message) ||
                        error.message ||
                        error.toString();

                    setErrorMessage(resMessage);
                    setIsLoading(false);
                }
            );

        // axios.post(`/api/test/${props.ipVer}`, data, {
        //     headers: authHeader()

        // }).then(res => {
        //     setIsLoading(false);
        //     //setSendMessage(res.data.message);
        //     //setModalShow(true);
        //     //props.history.push("/home");

        //     props.updateFun();
        //     props.handleCancel();
        //     form.resetFields();
        //     //window.location.reload();

        // }).catch(error => {
        //     setIsLoading(false);

        //     const errMessage =
        //         (error.response &&
        //             error.response.data &&
        //             error.response.data.message) ||
        //         error.message ||
        //         error.toString();

        //     setErrorMessage(errMessage);

        // });
        console.log();

    }



    const onModalCancel = () => {

        form.resetFields();
        setErrorMessage("")

        props.handleCancel();
    }

    const onChangePass = (e) => {

        setChangePass(e.target.checked)


    }



    const rules = (props.ipVer == "ipv4") ?


        [
            {
                required: true,
                message: 'Pole "Prefix" jest wymagane!!',
            },
            {
                pattern: /^((([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\.([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\.([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\.([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\/([0-9]|[12][0-9]|3[0-2]))$)/,
                //pattern: /^((([0-9a-fA-F]{1,4}:){7,7}[0-9a-fA-F]{1,4}|([0-9a-fA-F]{1,4}:){1,7}:|([0-9a-fA-F]{1,4}:){1,6}:[0-9a-fA-F]{1,4}|([0-9a-fA-F]{1,4}:){1,5}(:[0-9a-fA-F]{1,4}){1,2}|([0-9a-fA-F]{1,4}:){1,4}(:[0-9a-fA-F]{1,4}){1,3}|([0-9a-fA-F]{1,4}:){1,3}(:[0-9a-fA-F]{1,4}){1,4}|([0-9a-fA-F]{1,4}:){1,2}(:[0-9a-fA-F]{1,4}){1,5}|[0-9a-fA-F]{1,4}:((:[0-9a-fA-F]{1,4}){1,6})|:((:[0-9a-fA-F]{1,4}){1,7}|:)|fe80:(:[0-9a-fA-F]{0,4}){0,4}%[0-9a-zA-Z]{1,}|::(ffff(:0{1,4}){0,1}:){0,1}((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\.){3,3}(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])|([0-9a-fA-F]{1,4}:){1,4}:((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\.){3,3}(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9]))\/([0-9]|[1-9][0-9]|1[0-2][0-8])$)/,
                message: 'Podaj prawidłowy adres IP!'
            }
        ] :

        [
            {
                required: true,
                message: 'Pole "Prefix" jest wymagane!!',
            },
            {   //pattern: /^((([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\.([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\.([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\.([0-9]|[1-9][0-9]|1[0-9][0-9]|2[0-4][0-9]|25[0-5])\/([0-9]|[12][0-9]|3[0-2]))$)/, 
                pattern: /^((([0-9a-fA-F]{1,4}:){7,7}[0-9a-fA-F]{1,4}|([0-9a-fA-F]{1,4}:){1,7}:|([0-9a-fA-F]{1,4}:){1,6}:[0-9a-fA-F]{1,4}|([0-9a-fA-F]{1,4}:){1,5}(:[0-9a-fA-F]{1,4}){1,2}|([0-9a-fA-F]{1,4}:){1,4}(:[0-9a-fA-F]{1,4}){1,3}|([0-9a-fA-F]{1,4}:){1,3}(:[0-9a-fA-F]{1,4}){1,4}|([0-9a-fA-F]{1,4}:){1,2}(:[0-9a-fA-F]{1,4}){1,5}|[0-9a-fA-F]{1,4}:((:[0-9a-fA-F]{1,4}){1,6})|:((:[0-9a-fA-F]{1,4}){1,7}|:)|fe80:(:[0-9a-fA-F]{0,4}){0,4}%[0-9a-zA-Z]{1,}|::(ffff(:0{1,4}){0,1}:){0,1}((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\.){3,3}(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])|([0-9a-fA-F]{1,4}:){1,4}:((25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9])\.){3,3}(25[0-5]|(2[0-4]|1{0,1}[0-9]){0,1}[0-9]))\/([0-9]|[1-9][0-9]|1[0-2][0-8])$)/,
                message: 'Podaj prawidłowy adres IP!'
            }

        ]






    return (
        <div>




            <Modal
                visible={props.showModal}
                title="Edytuj użytkownika"
                onOk={handleOk}
                onCancel={onModalCancel}
                footer={[
                    <Button key="back" onClick={onModalCancel}>
                        Zamknij
            </Button>,
                    <Button key="submit" type="primary" form="basic" /*loading={loading}*/ onClick={handleOk} loading={isLoading}
                    >
                        Zapisz
                        </Button>
                ]}
            >

                {errorMessage && <Alert message={errorMessage} style={{ marginBottom: "10px" }} type="error" />}
                <Form
                    form={form}
                    {...layout}
                    //name="basic"
                    initialValues={{

                        ['roles']: props.roles,
                        changepass: false
                        //     remember: true,
                    }}
                    onFinish={formOk}
                //onFinishFailed={onFinishFailed}
                >



                    <Form.Item label="Nazwa użytkownika" name="username"

                        rules={[
                            {
                                required: true,
                                message: 'Pole "Nazwa użytkownika" jest wymagane!',
                            }]}
                    >
                        <Input style={{ color: "black" }} disabled={true} />
                    </Form.Item>

                    <Form.Item label="email" name="email"
                        rules={[
                            {
                                required: true,
                                message: 'Pole "emial" jest wymagane!',
                            }]}
                    >
                        <Input style={{ color: "black" }} /*defaultValue={props.node.description}*/ />
                    </Form.Item>

                    <Form.Item label="Role" name="roles">

                        <CheckboxGroup options={['user', 'moderator', 'admin']} />


                    </Form.Item>

                    <Form.Item label="Zmień hasło" name="changepass" valuePropName="checked">

                        <Checkbox onChange={onChangePass} />


                    </Form.Item>

                    <Form.Item
                        name="password"
                        label="Nowe hasło"
                        // rules={[
                        //     {
                        //         required: true,
                        //         message: 'Please input your password!',
                        //     },
                        // ]}
                        hasFeedback
                    >
                        <Input.Password disabled={!changePass}/>
                    </Form.Item>

                    <Form.Item
                        name="confirm"
                        label="Potwierdź nowe hasło"
                        dependencies={['password']}
                        hasFeedback
                        rules={[
                            // {
                            //     required: true,
                            //     message: 'Potwierdź password!',
                            // },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                    if (getFieldValue('password') === value) {
                                        return Promise.resolve();
                                    }

                                    return Promise.reject('Pola Password i Potwierdź Password nie pasują do siebie!');
                                },
                            }),
                        ]}
                    >
                        <Input.Password disabled={!changePass}/>
                    </Form.Item>




                </Form>

            </Modal>

        </div>
    )
}
