import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
//import './index.css';
import { Table, Input, InputNumber, Button, Space, Form } from 'antd';
import Highlighter from 'react-highlight-words';
import { SearchOutlined } from '@ant-design/icons';
import { EditOutlined, EditTwoTone, EditFilled, SaveOutlined, SaveFilled, CloseCircleOutlined } from '@ant-design/icons';

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode =  <Input disabled={title=="Prefix"?true:false} />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item

          //disabled={true}
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Uzupełnij pole ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const SearchTableFun = (props) => {

  const [form] = Form.useForm();
  const [data, setData] = useState([]);
  const [editingKey, setEditingKey] = useState('');

  const [searchText, setSearchText] = useState('');
  const [searchedColumn, setSearchedColumn] = useState('');

  var searchInput = useRef();

  useEffect(() => {

    console.log("EditableTable mount/update")

    setData(props.data.map(obj => ({ ...obj, touched: 'false' })))
    


  },[props.data])

  const isEditing = (record) => record.prefix === editingKey;

  const edit = (record) => {
    form.setFieldsValue({
      prefix: '',
      description: '',
      tag: '',
      ...record,
    });
    setEditingKey(record.prefix);
  };

  const cancel = () => {
    setEditingKey('');
  };

  const save = async (prefix) => {
    try {

      const row = await form.validateFields();
      const newData = [...data];
      const index = newData.findIndex((item) => prefix === item.prefix);

      if (index > -1 && (row.tag !== newData[index].tag || row.description !== newData[index].description)) {
        const item = newData[index];
        //meSetState();

        //const rowEdited = {...item,...row};
        newData.splice(index, 1, { ...item, ...row, touched: "true" });
        setData(newData);
        props.saveEditedRow(row);
        setEditingKey('');
      } else {
        //   newData.push(row);
        //   setData(newData);
        //   props.saveEditedRow(row);
        setEditingKey('');
      }
    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };



  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          //  ref={(node) => {
          //   searchInput = node;
          // }}

          ref={searchInput}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
              </Button>
          <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
              </Button>
        </Space>
      </div>
    ),
    filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase())
        : '',
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput.current.input.select(), 100);
      }
    },
    render: text =>
      searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[searchText]}
          autoEscape
          textToHighlight={text ? text.toString() : ''}
        />
      ) : (
          text
        ),
  });


  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex)
  };

  const handleReset = clearFilters => {
    clearFilters();
    setSearchText('');
  };

  const columns = [
    {
      title: 'Prefix',
      dataIndex: 'prefix',
      width: '25%',
      editable: true,
      ...getColumnSearchProps('prefix'),
    },
    {
      title: 'Description',
      dataIndex: 'description',
      width: '35%',
      editable: true,
      ...getColumnSearchProps('title'),
    },
    {
      title: 'Tag',
      dataIndex: 'tag',
      width: '20%',
      editable: true,
      ...getColumnSearchProps('tag'),
    },

    {
      title: 'Operation',
      dataIndex: 'operation',
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <a
              href="javascript:;"
              onClick={() => save(record.prefix)}
              style={{
                marginRight: 8,
              }}
            >
              <SaveOutlined />
            </a>
            {/* <Popconfirm title="Sure to cancel?" onConfirm={cancel}> */}
            <a href="javascript:;" onClick={cancel}><CloseCircleOutlined /></a>
            {/* </Popconfirm> */}
          </span>
        ) : (
            <a href="javascript:;" disabled={editingKey !== ''} onClick={() => edit(record)}>
              {record.touched == "true" ? <EditFilled /> : <EditOutlined />}
            </a>
          );
      },
    }
  ];

  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType: col.dataIndex === 'age' ? 'number' : 'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });

  return (
    <Form form={form} component={false}>
      <Table

        components={{
          body: {
            cell: EditableCell,
          },
        }}

        bordered
        columns={mergedColumns}
        dataSource={data}

        rowClassName="editable-row"
        pagination={{
          onChange: cancel,
        }} />
    </Form>
  )



}

export default SearchTableFun;