import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Input, Checkbox, Row } from 'antd';
import { PresetColorTypes } from 'antd/lib/_util/colors';

const CheckboxGroup = Checkbox.Group;

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 22,
  },
};

export default function ModaUserlShowDetails(props) {
  const [count, setCount] = useState(0);

  const [form] = Form.useForm();

  useEffect(() => {

    form.setFieldsValue({
      prefix: props.node.username,
      desc: props.node.email,
      createdAt: isoDateConverter(props.node.createdAt),
      updatedAt: isoDateConverter(props.node.updatedAt)



    })
  }, [props])

  const isoDateConverter = (isoDate) => {

    const date = new Date(isoDate);
    return date.getFullYear() + '-' + ("0" + (date.getMonth() + 1)).slice(-2) + '-' + ("0" + date.getDate()).slice(-2) + ' ' + ("0" + date.getHours()).slice(-2) + ":" + ("0" + date.getMinutes()).slice(-2) + ":" + ("0" + date.getSeconds()).slice(-2);


  }

  // Similar to componentDidMount and componentDidUpdate:  useEffect(() => {    // Update the document title using the browser API    document.title = `You clicked ${count} times`;  });
  return (
    <div>
      {/* <p>You clicked {count} times</p>
      <button onClick={() => setCount(count + 1)}>
        Click me
      </button> */}

      <Modal
        visible={props.showModal}
        title="User"
        //onOk={handleOk}
        onCancel={props.handleCancel}
        footer={[
          <Button key="back" onClick={props.handleCancel}>
            Zamknij
            </Button>,
          //   <Button key="submit" type="primary"  form="basic" loading={loading} /*onClick={handleOk}*/>
          //     Zapisz
          //     </Button>,
        ]}
      >

        {/* {errorMessage&&<Alert message={errorMessage} style={{ marginBottom: "10px" }} type="error" />} */}
        <Form
          form={form}
          {...layout}
          name="basic"
          initialValues={{
            
            ['roles']: props.roles
          }}
        // initialValues={{
        //   remember: true,
        // }}
        //onFinish={onFinish}
        // onFinishFailed={onFinishFailed}
        >

          <Form.Item label="Nazwa uzytkownika" name="username"
          // rules={[
          //     {
          //         required: true,
          //         message: 'Wubierz wersje IP!',
          //     }]}
          >
            <Input style={{ color: "black" }} disabled={true} defaultValue={props.node.username} />
          </Form.Item>

          <Form.Item label="email" name="emial"
          // rules={[
          //     {
          //         required: true,
          //         message: 'Wubierz wersje IP!',
          //     }]}
          >
            <Input style={{ color: "black" }} disabled={true} defaultValue={props.node.email} />
          </Form.Item>

          <Form.Item label="Role" name="roles">
            
          <CheckboxGroup disabled={true} options={['user', 'moderator', 'admin']} value={props.roles} onChange={()=> console.log("test")} />
           
       
          </Form.Item>


          <Form.Item label="Data utworznia" name="createdAt"
          // rules={[
          //     {
          //         required: true,
          //         message: 'Wubierz wersje IP!',
          //     }]}
          >
            <Input style={{ color: "black" }} disabled={true} defaultValue={isoDateConverter(props.node.createdAt)} />
          </Form.Item>

          <Form.Item label="Ostania modyfikacja" name="updatedAt"
          // rules={[
          //     {
          //         required: true,
          //         message: 'Wubierz wersje IP!',
          //     }]}
          >
            <Input style={{ color: "black" }} disabled={true} defaultValue={isoDateConverter(props.node.updatedAt)} />
          </Form.Item>

        </Form>

      </Modal>
    </div>
  );
}