import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Input, Radio,  Alert  } from 'antd';
import UserService from "../../services/user.service";

const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 22,
    },
};

export default function ModalDBEdit(props) {
    //const [count, setCount] = useState(0);
    const [loading, setIsLoading] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const [message, setMessage] = useState("");

    const [form] = Form.useForm();

    useEffect(() => {

        form.setFieldsValue({
            type: props.node.type,
            subType: props.node.subType, 
            description: props.node.description
        })}
            , []);


        const handleOk = (values) => {

            form.submit();

        };

        const formOk = () => {

            setIsLoading(true);
            let data = form.getFieldsValue();
            UserService.patchDbs(data)
                .then(
                    (response) => {
                        setMessage(response.data.message);
                        setIsLoading(false);
                        setErrorMessage("");
                        props.handleCancel()
                        props.getDBs()
                    },
                    (error) => {
                        const resMessage =
                            (error.response &&
                                error.response.data &&
                                error.response.data.message) ||
                            error.message ||
                            error.toString();

                        setErrorMessage(resMessage);
                        setIsLoading(false);
                    }
                );


            console.log();

        }



        const onModalCancel = () => {

            form.resetFields();
            setErrorMessage("")

            props.handleCancel();
        }



        // Similar to componentDidMount and componentDidUpdate:  useEffect(() => {    // Update the document title using the browser API    document.title = `You clicked ${count} times`;  });
        return (
            <div>
                {/* <p>You clicked {count} times</p>
      <button onClick={() => setCount(count + 1)}>
        Click me
      </button> */}

                <Modal
                    visible={props.showModal}
                    title="Baza"
                    onOk={handleOk}
                    onCancel={onModalCancel}
                    footer={[
                        <Button key="back" onClick={props.handleCancel}>
                            Zamknij
            </Button>,
                        <Button key="submit" type="primary" form="basic" loading={loading} onClick={handleOk}>
                            Zapisz
                        </Button>,
                    ]}
                >

                    {errorMessage&&<Alert message={errorMessage} style={{ marginBottom: "10px" }} type="error" />}
                    <Form
                        form={form}
                        {...layout}
                        name="basic"
                        // initialValues={{
                        //   remember: true,
                        // }}
                        onFinish={formOk}
                    // onFinishFailed={onFinishFailed}
                    >

                        <Form.Item label="Wersja IP" name="type"
                        // rules={[
                        //     {
                        //         required: true,
                        //         message: 'Wubierz wersje IP!',
                        //     }]}
                        >
                            <Input style={{ color: "black" }} disabled={true}   />
                        </Form.Item>

                        <Form.Item label="Nazwa bazy" name="subType"
                        // rules={[
                        //     {
                        //         required: true,
                        //         message: 'Podaj nazwę bazy!',
                        //     }]}
                        >
                            <Input style={{ color: "black" }} disabled={true}  />
                        </Form.Item>

                        <Form.Item label="Opis" name="description"
                            rules={[
                                {
                                    required: true,
                                    message: 'Podaj opis bazy!',
                                }]}
                        >
                            <Input style={{ color: "black" }}  />
                        </Form.Item>


                    </Form>

                </Modal>
            </div>
        )
    
}