import React, { useState, useEffect } from "react";
import { Select, Spin, Row, Tree, Menu, Dropdown, Tabs } from 'antd';
import styles from './AdminPortal.module.css';

import ModalDBShowDetails from "./modals/ModalDBShowDetails";
import ModalDBCreate from "./modals/ModalDBCreate";
import ModalDBEdit from "./modals/ModalDBEdit";
import ModalDBDelete from "./modals/ModalDBDelete";
import ModalUserShowDetails from "./modals/ModalUserShowDetails";
import ModalUserCreate from "./modals/ModalUserCreate";
import ModalUserDelete from "./modals/ModalUserDelete";
import ModalUserEdit from "./modals/ModalUserEdit";
import 'antd/dist/antd.css';


import UserService from "../services/user.service";

const { TabPane } = Tabs;
const { Option } = Select;

const AdminPortal = () => {
    const [mgmtScope, setMgmtScope] = useState("db")
    const [content, setContent] = useState("");
    const [loading, setLoading] = useState(false);
    const [selectedNode, setSelectedNode] = useState(null);
    const [showUserModalShowDetails, setShowUserModalShowDetails] = useState(false)
    const [showDBModalShowDetails, setShowDBModalShowDetails] = useState(false)
    const [showDBModalCreate, setShowDBModalCreate] = useState(false)
    const [showDBModalEdit, setShowDBModalEdit] = useState(false)
    const [showDBModalDelete, setShowDBModalDelete] = useState(false)
    const [showUserModalCreate, setShowUserModalCreate] = useState(false)
    const [showUserModalDelete, setShowUserModalDelete] = useState(false)
    const [showUserModalEdit, setShowUserModalEdit] = useState(false)
    const [roles, setRoles] = useState([])
    const [error, setError] = useState("");



    useEffect(() => {
        setLoading(true)

        UserService.getDbs("both").then(
            (response) => {
                setContent(response.data);
                setLoading(false)
            },
            (error) => {
                const _content =
                    (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                    error.message ||
                    error.toString();

                setError(_content);
                setLoading(false)
            })
        // UserService.getDbs("both").then(
        //     (response) => {
        //         setContent(response.data);
        //     },
        //     (error) => {
        //         const _content =
        //             (error.response &&
        //                 error.response.data &&
        //                 error.response.data.message) ||
        //             error.message ||
        //             error.toString();

        //         setContent(_content);
        // }
        // );
    }, []);

    const handleChange = (e) => {
        setMgmtScope(e)
        setLoading(true)

        if (e == "db") {

            getDBs();

        }

        else if (e == "user") {

            getUsers();

        }


    }

    const getDBs = () => {

        UserService.getDbs("both").then(
            (response) => {
                setContent(response.data);
                setLoading(false)
            },
            (error) => {
                const _content =
                    (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                    error.message ||
                    error.toString();

                setError(_content);
                setLoading(false)
            }
        );

    }

    const getUsers = () => {

        UserService.getUserBoard()
            .then((response) => {
                setContent(response.data.sort((a, b) => a.username.localeCompare(b.username)))
                setLoading(false)
            })
            .catch(error => {

                const _content =
                    (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                    error.message ||
                    error.toString();

                setError(_content);
                setLoading(false);

            })


    }


    const onClick = ({ e, node }) => {


        setSelectedNode(node)
        if (mgmtScope == "user")
            setRoles(node.roles.map(item => item.name))

        //console.log(e )
        //setVisableMenu(true);
        //setMenuX(e.event.pageX);
        //setMenuY(e.event.pageY);

    };

    const onShowUserDatailsClick = ({ item, key, keyPath, domEvent }) => {

        setShowUserModalShowDetails(true);


    }


    const onShowDBDatailsClick = ({ item, key, keyPath, domEvent }) => {

        setShowDBModalShowDetails(true);


    }

    const onShowDBCreateClick = ({ item, key, keyPath, domEvent }) => {

        setShowDBModalCreate(true);


    }

    const onShowDBEditClick = ({ item, key, keyPath, domEvent }) => {

        setShowDBModalEdit(true)
    }

    const onShowDBDeleteClick = ({ item, key, keyPath, domEvent }) => {

        setShowDBModalDelete(true)
    }


    const onUserCreateClick = ({ item, key, keyPath, domEvent }) => {

        setShowUserModalCreate(true);

    }

    const onUserDeleteClick = ({ item, key, keyPath, domEvent }) => {

        setShowUserModalDelete(true);

    }

    const onUserEditClick = ({ item, key, keyPath, domEvent }) => {

        setShowUserModalEdit(true);

    }

    const handleCancelUserShowDetails = () => {

        setShowUserModalShowDetails(false);

    }

    const handleCancelDBShowDetails = () => {

        setShowDBModalShowDetails(false);

    }

    const handleCancelUserCreate = () => {

        setShowUserModalCreate(false);

    }

    const handleCancelDBCreate = () => {

        setShowDBModalCreate(false);

    }

    const handleCancelDBEdit = () => {

        setShowDBModalEdit(false)

    }

    const handleCancelDBDelete = () => {

        setShowDBModalDelete(false)

    }



    const handleCancelUserDelete = () => {

        setShowUserModalDelete(false);

    }
    const handleCancelUserEdit = () => {

        setShowUserModalEdit(false);

    }

    const menudbparent = (nodeData) => (

        <Menu nodedata={nodeData} >

            <Menu.Item onClick={onShowDBCreateClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Dodaj nową bazę
                </a>
            </Menu.Item>

        </Menu>




    )



    const menudbchild = (nodeData) => (
        <Menu nodedata={nodeData} >
            <Menu.Item onClick={onShowDBDatailsClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Pokaż szczegóły bazy
                </a>
            </Menu.Item>

            <Menu.Item onClick={onShowDBEditClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Edytuj bazę
                </a>
            </Menu.Item>
            <Menu.Item onClick={onShowDBDeleteClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Usuń bazę
                </a>
            </Menu.Item>


        </Menu>
    );


    const menuuser = (nodeData) => (
        <Menu nodedata={nodeData} >
            <Menu.Item onClick={onShowUserDatailsClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Pokaż szczegóły użytkownika
                </a>
            </Menu.Item>

            <Menu.Item onClick={onUserEditClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Edytuj użytkownika
                </a>
            </Menu.Item>
            <Menu.Item onClick={onUserDeleteClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Usuń użytkownika
                </a>
            </Menu.Item>
            <Menu.Item onClick={onUserCreateClick}>
                <a target="_blank" rel="noopener noreferrer" >
                    Dodaj nowego użytkownika
                </a>
            </Menu.Item>

        </Menu>
    );

    return (
        <div className="container">


            <Tabs onTabClick={handleChange}>
                <TabPane tab="Bazy" key="db">

                    {loading ? <Spin tip="Ładowanie..." /> :

                        <Tree treeData={content}

                            titleRender={
                                (nodeData) => (!nodeData.children ? <Dropdown className={styles.dropdown} trigger={["contextMenu"]} overlay={menudbchild(nodeData)} >
                                    <a >
                                        {nodeData.title}
                                    </a>
                                </Dropdown> :

                                    <Dropdown className={styles.dropdown} trigger={["contextMenu"]} overlay={menudbparent(nodeData)} >
                                        <a >
                                            {nodeData.title}
                                        </a>
                                    </Dropdown>

                                )

                            }


                            onRightClick={onClick}



                        />
                    }

                </TabPane>
                <TabPane tab="Użytkownicy" key="user">


                    {loading ? <Spin tip="Ładowanie..." /> :

                        <Tree treeData={content}

                            titleRender={

                                (nodeData) => (!nodeData.children ? <Dropdown className={styles.dropdown} trigger={["contextMenu"]} overlay={menuuser(nodeData)} >
                                    <a >
                                        {nodeData.title}
                                    </a>
                                </Dropdown> : nodeData.title)

                            }

                            onRightClick={onClick}

                        />
                    }
                </TabPane>

            </Tabs>

            {/* <Row gutter={[0, 16]}>
                <Select placeholder="Wybierz" style={{ width: 150 }} onChange={handleChange}>
                    <Option value="db">Bazy danych</Option>
                    <Option value="user">Użytkownicy</Option>
                </Select>
            </Row>
            <Row>

                {loading ? <Spin tip="Ładowanie..." /> :

                    <Tree treeData={content}

                        titleRender={mgmtScope == "user" ?

                            (nodeData) => (!nodeData.children ? <Dropdown className={styles.dropdown} trigger={["contextMenu"]} overlay={menuuser(nodeData)} >
                                <a >
                                    {nodeData.title}
                                </a>
                            </Dropdown> : nodeData.title)
                            :
                            (nodeData) => (!nodeData.children ? <Dropdown className={styles.dropdown} trigger={["contextMenu"]} overlay={menudb(nodeData)} >
                                <a >
                                    {nodeData.title}
                                </a>
                            </Dropdown> : nodeData.title)

                        }


                        onRightClick={onClick}



                    />
                } */}
            {/* 
            </Row> */}





            {showDBModalShowDetails ? <ModalDBShowDetails showModal={showDBModalShowDetails} node={selectedNode} handleCancel={handleCancelDBShowDetails} /> : null}
            {showDBModalCreate ? <ModalDBCreate showModal={showDBModalCreate} handleCancel={handleCancelDBCreate} getDBs={getDBs} /> : null}
            {showDBModalEdit ? <ModalDBEdit showModal={showDBModalEdit} node={selectedNode} handleCancel={handleCancelDBEdit} getDBs={getDBs} /> : null}
            {showDBModalDelete ? <ModalDBDelete showModal={showDBModalDelete} node={selectedNode} handleCancel={handleCancelDBDelete} getDBs={getDBs} /> : null}
            {showUserModalShowDetails ? <ModalUserShowDetails showModal={showUserModalShowDetails} roles={roles} node={selectedNode} handleCancel={handleCancelUserShowDetails} /> : null}
            {showUserModalCreate ? <ModalUserCreate showModal={showUserModalCreate} node={selectedNode} handleCancel={handleCancelUserCreate} getUsers={getUsers} /> : null}
            {showUserModalDelete ? <ModalUserDelete showModal={showUserModalDelete} roles={roles} node={selectedNode} handleCancel={handleCancelUserDelete} getUsers={getUsers} /> : null}
            {showUserModalEdit ? <ModalUserEdit showModal={showUserModalEdit} roles={roles} node={selectedNode} handleCancel={handleCancelUserEdit} getUsers={getUsers} /> : null}
        </div>
    );
};

export default AdminPortal;